
.modal {
  text-align: left;
}
.modal-card {
  width: 400px;
}
.modal-card-head {
  background-color: inherit;
  border-color: inherit;
  text-align: left;
  padding: 0;
  padding-bottom: 8px;
}
.modal-card-title {
  color: white;
}
.modal-card-body {
  border-radius: 4px;
  text-align: center;
}
i.fa-trophy {
  color: gold;
}
i.fa-circle {
  margin: 2px;
}
i.gold {
  color: gold;
}



